.loginModal {
	position: fixed;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	z-index: 7;
}

.loginModal-overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.4);
}

.loginModal-content {
	position: fixed;
	left: 50%;
	top: 50%;
	min-width: 40%;
	max-width: 80%;
	transform: translate(-50%, -50%);
	background: #fff;
	border-radius: 15px;
	padding: 40px;
	text-align: center;
}

.loginModal-exit {
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 15px;
	cursor: pointer;
	transition: 0.3s;
}

.loginModal-exit:hover {
	color: #d43d3d;
}

.loginModal-content h2 {
	text-align: center;
	margin-bottom: 0;
}

.loginModal-content h4 {
	text-align: center;
	font-weight: normal;
	margin-top: 0;
}

.loginModal-username {
	width: 100%;
	padding: 8px 0px 3px 5px;
	font-size: 17px;
	box-sizing: border-box;
	border: none;
	border-bottom: 1px solid #555;
	transition: 0.3s;
	background-image: url('./user.png');
	background-repeat: no-repeat;
	background-position: right center;
	background-size: auto 70%;
}

.loginModal-username:focus {
	outline: none;
	border-bottom: 1px solid var(--login-btn);
}

#lockIcon {
	font-size: 50px;
	margin: 20px auto;
	height: 100px;
	width: 100px;
}

#lockIcon i {
	padding: 25px;
	border-radius: 50%;
	border: 3px solid;
}

.loginModal-unlock-icon {
	color: #0da141;
	border-color: #0da141;
}

.loginModal-lock-icon {
	color: #333;
	border-color: #333;
}

.loginModal-content button {
	font-size: 16px;
	font-weight: bold;
	width: 70%;
	padding: 12px;
	margin: 10px 0px;
	outline: none;
	border: none;
	background: var(--login-btn);
	cursor: pointer;
	border-radius: 15px;
}

.loginModal-content button:disabled {
	opacity: 0.4;
	cursor: not-allowed;
}

#loginModal-success-toast {
	visibility: hidden;
	position: fixed;
	bottom: 20px;
	left: 50%;
	transform: translateX(-50%);
	text-align: center;
	background: #fff;
	color: #000;
	padding: 15px 25px;
	z-index: 10;
	font-size: 17px;
	border-radius: 15px;
	box-shadow: 2px 2px 6px 1px #333;
}

#loginModal-success-toast i {
	font-size: 22px;
	vertical-align: middle;
	color: #0da141;
	margin-right: 15px;
}

#loginModal-success-toast.show {
	visibility: visible;
	animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

#loginModal-fail-toast {
	visibility: hidden;
	position: fixed;
	bottom: 20px;
	left: 50%;
	transform: translateX(-50%);
	text-align: center;
	background: #d43d3d;
	color: #fff;
	padding: 15px 25px;
	z-index: 10;
	font-size: 17px;
	border-radius: 15px;
	box-shadow: 2px 2px 6px 1px #333;
}

#loginModal-fail-toast i {
	font-size: 22px;
	vertical-align: middle;
	margin-right: 15px;
}

#loginModal-fail-toast.show {
	visibility: visible;
	animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 20px; opacity: 1;}
}

@keyframes fadeout {
  from {bottom: 20px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}